<template>
  <div id='app'>
    <div class='app-phone'>
      <div class='phone-header'>
        <img
          src='https://upload.wikimedia.org/wikipedia/commons/thumb/2/2a/Instagram_logo.svg/1200px-Instagram_logo.svg.png'
          alt='logo'
        >
        
        <a class='cancel-cta' v-if='step===2 || step===3' @click='goToHome'>Cancel</a>
        
        <a class='next-cta' v-if='step===2' @click='step++'>Next</a>
        
        <a class='next-cta' v-if='step===3' @click='sharePost'>Share</a>
      </div>
      <phone-body
        :step='step'
        :posts='posts'
        :filters='filters'
        :image='image'
        :selectedFilter='selectedFilter'
        v-model='caption'
      ></phone-body>
      <div class='phone-footer'>
        <div class='home-cta' @click='goToHome'>
          <i class='fas fa-home'></i>
        </div>
        <div class='upload-cta'>
          <input
            type='file'
            name='file'
            id='file'
            class='inputfile'
            @change='uploadImage'
            :disabled='step!==1'
          >
          <label for='file'>
            <i class='fas fa-plus-square fa-lg'></i>
          </label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PhoneBody from './components/PhoneBody.vue';
import posts from './data/posts.js';
import filters from './data/filters.js';

import EventBus from './event-bus.js';

export default {
  name: 'App',
  data() {
    return {
      step: 1,
      posts,
      filters,
      image: '',
      selectedFilter: '',
      caption: ''
    };
  },
  created() {
    EventBus.$on('filter-selected', evt => {
      this.selectedFilter = evt.filter;
    });
  },
  methods: {
    goToHome() {
      this.image = '';
      this.selectedFilter = '';
      this.caption = '';
      this.step = 1;
    },
    sharePost() {
      const post = {
        username: 'me3',
        userImage:
          'https://s3-us-west-2.amazonaws.com/s.cdpn.io/1211695/me_3.jpg',
        postImage: this.image,
        likes: 0,
        caption: this.caption,
        filter: this.selectedFilter
      };
      this.posts.unshift(post);
      this.goToHome();
    },
    uploadImage(evt) {
      const files = evt.target.files;
      if (!files.length) return;

      const reader = new FileReader();
      reader.readAsDataURL(files[0]);
      reader.onload = evt => {
        this.image = evt.target.result;
        this.step = 2;
      };
      document.querySelector('#file').value = '';
    }
  },
  components: {
    'phone-body': PhoneBody
  }
};
</script>

<style lang='scss' src='./styles/app.scss'>
</style>
